import { Button } from 'antd'
import React, { useState, useEffect } from 'react'
import MyEditor from "../../component/RichEdit"

function Index() {
  const [html, setHtml] = useState('')

  // 模拟 ajax 请求，异步设置 html
  useEffect(() => {
    setTimeout(() => {
      setHtml('<p>hello world</p>')
    }, 1500)
  }, [])


  return (
    <div>
      <div>
        富文本编辑器
      </div>

      <MyEditor
        html={html}
        setHtml={setHtml}
      />

      <Button onClick={() => {
        setHtml('')
      }}>清空</Button>

    </div>
  )
}

export default Index
